// Tektronix grid variables based on bootsrap 4 grid.
$mobile-landscape: 576px; // $screen-sm
$tablet: 768px; // $screen-md
$desktop: 992px; //$screen-lg
$desktop-wide: 1280px; //$screen-xl
$mobile-tablet-only: 'screen' 'max-width' $desktop - 1;
$mobile-only: 'screen' 'max-width' $tablet - 1;
$phone-only:  'screen' 'max-width' $mobile-landscape - 1;
$mobile-landscape-tablet-only: $mobile-landscape $desktop - 1;

$image-path: '../images/';
// Typography.
$base-font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
$heading-font-family: 'Neubau Grotesk Light', sans-serif;

// Font Sizes.
$base-font-size: (18px, 20px, 20px, 20px);
$base-font: 16px;

// Line height.
// http://meyerweb.com/eric/thoughts/2006/02/08/unitless-line-heights.
$base-line-height: 1.4;
$heading-line-height: 1.2;

// Other Sizes.
$base-border-radius: 20px;
$base-spacing: $base-line-height * 1em;
$small-spacing: $base-spacing / 2;
$large-spacing: $base-spacing * 2;
$base-z-index: 0;
$side-margin: 15;
$max-width: 32rem;
$gutter: 1/3;

$headerHeight: 65px;
$headerStickyPhone: 104px;
$inputHeight: 35px;

$toolbarTabHeight: 39px;
$toolbarMenuHeight: 47px;
$headerIconSize: 24px;

// Colors.
$white: #ffffff;
$blue: #1565c0;
$dark-gray: #333;
$medium-gray: #999;
$light-gray: #ddd;
$gray-f2: #f2f2f2;
