// Custom mixins.
// =============================================================================
// Font Face
// =============================================================================
@mixin grotesk-light {
  font-family: 'Neubau Grotesk Light', $helvetica;
}

@mixin grotesk-regular {
  font-family: 'Neubau Grotesk Regular', $helvetica;
}

@mixin grotesk-bold {
  font-family: 'Neubau Grotesk Bold', $helvetica;
}

@mixin gothic {
  font-family: 'Avant Garde Gothic', $helvetica;
}

@mixin gothic-light {
  font-family: 'Avant Garde Gothic Light', $helvetica;
}


@mixin helvetica-arial-regular {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
}

@mixin helvetica-arial-light {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 300;
}

@mixin helvetica-arial-light-italic {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-style: italic;
  font-weight: 300;
}

@mixin helvetica-arial-book {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
}

@mixin helvetica-arial-semibold {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 600;
}

@mixin helvetica-arial-bold {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 700;
}

@mixin fixed-positioned {
  position: fixed;
  width: 100%;
  left: 0;
}

@mixin mobile-menu-style {
  display: block;
  color: $white;
  font-size: rem(13px);
  letter-spacing: rem(0.6px);
  line-height: 1;
  position: relative;
  padding: rem(20px) rem($grid-gutter-width/2);
  text-transform: uppercase;
  text-decoration: none;

  &:before {
    @extend %icons;
    @extend .icon-chevron-down;
    font-size: rem(14px);
    position: absolute;
    right: rem($grid-gutter-width/2);
    top: 50%;
    margin-top: rem(-7px);
  }

  &:hover {
    text-decoration: none;
  }
}

@mixin sr-only {
  position: absolute;
  width: rem(1px);
  height: rem(1px);
  margin: rem(-1px);
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

@mixin underlined-header($bg-col: $grey-3 ) {
  &:after {
    content: '';
    display: block;
    width: rem(40px);
    height: rem(1px);
    background-color: $bg-col;
    margin-top: rem(15px);
  }
}

// Define placeholders that contain mixins.
%date {
  @include helvetica-arial-light;
  color: $lightest-grey;
}

%section-title {
  @include helvetica-arial-book;
  color: $medium-gray;
  font-size: rem(14px);
  text-transform: uppercase;
}

%featured-title {
  @include helvetica-arial-light;
  color: $base-font-color;
  display: block;
  font-size: rem(22px);
}

%dropdown-btn {
  position: relative;
  z-index: 20;

  ul {
    @extend %reset-list;
    @include transition(opacity 0.3s ease-out);
    background-color: $white;
    display: block;
    width: 90%;
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -45%; // Half dropdown's width.

    a {
      @include helvetica-arial-light;
      color: $base-font-color;
      display: block;
      font-size: rem(12px);
      padding: rem(5px) rem(20px);

      &:hover {
        background-color: $tk-blue;
        color: $white;
        text-decoration: none;
      }
    }
  }

  &.opened {
    ul {
      border: rem(1px) solid rgba(0,0,0,0.15);
      box-shadow:  0 rem(3px) rem(6px) rgba(0,0,0,0.2);
      max-height: rem(700px);
      overflow-y: auto;
      opacity: 1;
    }

    .icon-chevron-down {
      &:before {
        content: '\7d';
      }
    }
  }

  .btn--green {
    @include helvetica-arial-book;
    letter-spacing: rem(0.6px);
    padding: rem(8px) rem(40px) rem(8px) rem(15px);

    &,
    &:hover {
      background-color: $mid-green;
    }

    &:before {
      font-size: rem(15px);
    }
  }

  &.dropdown--icon {
    ul {
      width: rem(100px);
      margin-left: rem(-50px); // Half width.
    }
  }
}

@mixin colored-border {
  position: relative;

  &:before {
    @include linear-gradient(left, $vibrant-blue, $mid-green);
    content: '';
    position: absolute;
    width: 100%;
    height: rem(2px);
    bottom: 0;
    left: 0;
  }
}
@mixin steps-arrowed-nav ($arrow_left_color) {
  position: absolute;
  top: 0;
  display: block;
  border-left: rem(25px) solid $arrow-left-color; 
  border-top: rem(17px) solid transparent; 
  border-bottom: rem(17px) solid transparent;
  width:0;
  height:0;
  content: " ";
}
