/**
 * @file
 * sass/partials/common/_placeholders.scss
 */

%side-paddings-md {
  @include breakpoint($tablet) {
    padding-left: $grid-gutter-width/2;
    padding-right: $grid-gutter-width/2;
  }
}

%outer-container {
  max-width: $max-width;
  margin-left: auto;
  margin-right: auto;
}

%clearfix {
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

%row-sm {
  @include breakpoint($mobile-only) {
    margin-left: rem(-$grid-gutter-width/2);
    margin-right: rem(-$grid-gutter-width/2);
  }
}

%horizontal-nav {
  ul {
    margin-top: 0;
    width: 100%;
    text-align: center;
  }
  li {
    display: inline-block;
    padding-left: $small-spacing;
  }
  a {
    color: $medium-gray;
    text-decoration: none;
    &:active,
    &:focus,
    &:hover {
      color: $dark-gray;
    }
  }
  .is-active {
    color: $dark-gray;
  }
}

%breadcrumb {
  @extend %reset-list;
  color: $lightest-grey;
  font-size: rem(12px);
  line-height: rem(18px);

  li {
    display: inline-block;

    &:before {
      content: '\002F';
      display: inline-block;
      color: $lightest-grey;
      padding: 0 rem(5px);
    }

    &:first-child {
      &:before {
        content: none;
      }
    }
  }

  a {
    &,
    &:hover {
      color: $lightest-grey;
    }
  }
}

%center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

%align-icon {
  display: inline-block;
  margin-left: rem(5px);
  vertical-align: middle;
}

%center-absolute {
  @include transform(translate(-50%, -50%));
  left: 50%;
  top: 50%;
  position: absolute;
}

//reset list styles
%reset-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

%list--blue-points {
  list-style-type: none;

  li {
    position: relative;
    padding-left: rem(14px);
    margin-bottom: rem(10px);

    &:before {
      font-size: rem(13px);
      content: "\2022";
      display: block;
      position: absolute;
      left: 0;
      color: $vibrant-blue;
    }
  }
}

%small-font {
  font-size: rem(14px);
  line-height: rem(22px);
}

%color-grey {
  color: $light-grey !important;
}

%color-red {
  color: $red1 !important;
}

%color-orange {
  color: $orange !important;
}

%color--blue {
  color: $vibrant-blue !important;
}

%margin-bottom {
  margin-bottom: rem(20px);

  @include breakpoint($tablet) {
    margin-bottom: rem(40px);
  }
}

%padding-top {
  padding-top: rem(20px);

  @include breakpoint($tablet) {
    padding-top: rem(40px);
  }
}

%padding-v {
  padding-top: rem(20px);
  padding-bottom: rem(20px);

  @include breakpoint($tablet) {
    padding-top: rem(40px);
    padding-bottom: rem(40px);
  }
}

%thumb-link-icon {
  display: block;
  border-bottom: rem(1px) solid $grey-light-border;
  position: relative;
  padding: rem(15px) 0 rem(15px) rem(50px);

  &:before {
    color: $grey-light-border;
    position: absolute;
    left: rem(10px);
    top: 50%;
    margin-top: rem(-15px);
    font-size: rem(30px);
  }

  &:hover {
    background-color: $blue-hover;

    &:before {
      color: $white;
    }
  }

  @include breakpoint($tablet) {
    border: rem(1px) solid $blue-hover;
    padding:  rem(20px) rem(10px);
    text-align: center;

    &:before {
      color: $white;
      display: block;
      font-size: rem(50px);
      margin: 0 0 rem(20px);
      position: static;
    }
  }
}

// Links with icons.
%top-icon {
  display: inline-block;
  position: relative;

  &:active,
  &:focus,
  &:hover {
    text-decoration: none;
  }

  &:before {
    display: block;
    font-size: rem(50px);
    text-align: center;
    margin-bottom: rem(15px);
  }

  .dropdown--icon & {
    padding-right: rem(14px);// Space for triangle.

    &:after {
      @include triangle(10px, $vibrant-blue, down);
      content: '';
      display: block;
      position: absolute;
      bottom: rem(8px);
      right: 0;
    }

    &:hover:after {
      border-top-color: $link-hover;
    }
  }

  .opened &:after {
    border-bottom: 5px solid $link-hover;
    border-top: 0;
  }
}

%left-icon {
  display: inline-block;
  padding-left: rem(27px);
  position: relative;

  &:before {
    @extend %icons;
    font-size: rem(21px);
    left: 0;
    position: absolute;
    top: 0;
  }
}

%video-icon {
  display: block;
  position: relative;

  &:before {
    @include triangle(16px, $black, right);
    z-index: 2;
  }

  &:after {
    box-shadow: inset 0px 1px 15px 4px rgba(0,0,0,0.75);
    background-color: rgba(255,255,255,0.75);
    border-radius: 100%;
    height: rem(40px);
    width: rem(40px);
  }

  &:before,
  &:after {
    @extend %center-absolute;
    display: block;
    content: '';
  }
}

// Display one/two/three items per row for phone/mobile_and_tablet/desktop breakpoints.
%responsive-align-cards {
  @include display(flex);
  @include flex-wrap(wrap);
}

%separator--right {
  &:after {
    content: '\007C';
    color: $base-border-color;
    display: inline-block;
    margin: 0 rem(10px);
  }

  &:last-child {
    &:after {
      content: none;
    }
  }
}

%border-right-md {
  @include breakpoint($tablet) {
    border-right: $base-border !important;
  }
}

// Table styles.
%bordered {
  td,
  th {
    border: rem(1px) solid $light-gray;
    padding: rem(8px);
  }

  th {
    border-bottom-width: rem(2px);
  }
}

// Responsive video.
%responsive-video {
  position: relative;

  &:after {
    content: '';
    display: block;
    padding-bottom: 56.25%;
  }

  iframe,
  object,
  .video-js {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0;
  }
}

%scale-img {
  img {
    @include transition(transform 0.5s);
    @include transform(scale(1));
  }

  &:hover {
    img {
      @include transform(scale(1.05));
    }
  }
}

%title {
  font-size: rem(30px);

  @include breakpoint($tablet) {
    font-size: rem(36px);
  }
}

// Pseudo element that comes out of container.
%out-of-container {
  position: relative;

  &:before {
    @include transform(translateX(-50%));
    content: '';
    left: 50%;
    position: absolute;
    width: 100vw;
    top: 0;
    height: 100%;
    z-index: -1;
  }
}
